// 
// Projects
// 

.gs-steps-section {
    background-color: rgba(28, 82, 82, 1);
    padding: 2rem 0;

    .featured-text {
        padding: 2rem;

        @include media-breakpoint-up(lg) {
            padding: 0 0 0 2rem;
            border-left: 0.5rem solid $primary;
        }
    }

    .project-text {

        @include media-breakpoint-up(lg) {

            hr {
                border-color: $primary;
                border-width: 0.25rem;
                width: 30%;
            }
        }
    }
}