
   @import "customs";
   @import "mixins";

   #map {top:0; bottom:0; height: 100vh; }

   svg {
     position: absolute;
     width: 100%;
     height: 100%;
   }

    circle {
       stroke-width: 2;
       cursor: pointer;
       transition: 0.5s fill, 0.5s stroke-width;
   }
   circle:hover {
       fill: #5e5ee5;
       stroke-width: 18;
   } 



#show_infos{
  position:absolute;
  bottom: $grid-gutter-width*8;
  right:$grid-gutter-width;
  text-decoration: none;
  color: white;
  border: 1px solid;
  border-radius: 10px;
  padding: $grid-gutter-width/3;
  font-size: $grid-gutter-width/1.5;
  font-family: Chillax-variable;
  backdrop-filter: blur(2px) contrast(60%);
            -webkit-backdrop-filter: blur(2px) contrast(60%);
            
            background-color: rgba(225, 225, 225, .2);
            border-radius: 10px;
}


#swap_giardinopersonale{
  position:absolute;
  bottom: $grid-gutter-width*4;
  right:$grid-gutter-width;
  text-decoration: none;
  color: white;
  border: 1px solid;
  border-radius: 10px;
  padding: $grid-gutter-width/3;
  font-size: $grid-gutter-width/1.5;
  font-family: Chillax-variable;
  backdrop-filter: blur(2px) contrast(60%);
            -webkit-backdrop-filter: blur(2px) contrast(60%);
            
            background-color: rgba(225, 225, 225, .2);
            border-radius: 10px;
}



   .gos-general-infotab {
      display: fixed;
      background-color: transparent;
      width: 100vw;
      height: 70vh;
      position: absolute;
      top:0;
      left:0;
      webkit-backdrop-filter: blur(10px);
      backdrop-filter: blur(10px);
      background-color: rgba(255, 255, 255, 0.5);
      border-radius: 5px;
      
      @include media-breakpoint-up(lg) {
      width: 55vw;
      height: 100%;
      position: absolute;
      /* top:$grid-gutter-width;
      left:$grid-gutter-width ;  */         
    }

    .gos-general-infotab-container{

      .info-inner{
        font-size: $grid-gutter-width*0.75;
        font-family: Chillax-variable;
        font-weight: 300;
        
        h2{
          font-family: Chillax-variable;
          font-weight: 400;
          font-size: $grid-gutter-width;

        }

        .info{
          font-family: work sans;
          font-size: $grid-gutter-width*0.75        }
      }

    }

}