// scss-docs-start color-variables
$blue:    #0d6efd !default;
$indigo:  #6610f2 !default;
$purple:  #6f42c1 !default;
$pink:    #d63384 !default;
$red:     #dc3545 !default;
$orange:  #fd7e14 !default;
$yellow:  #E6F47B !default;
$light-green:   #6F9283 !default;
$green:   #093824 !default;
$teal:    #20c997 !default;
$cyan:    #EAF6FF !default;
$white:    #ffffff !default;
$white-transparent:    #ffffff39 !default;
$green-transparent:    #1987548e !default;

// scss-docs-start colors-map
$colors: (
  "blue":       $blue,
  "indigo":     $indigo,
  "purple":     $purple,
  "pink":       $pink,
  "red":        $red,
  "orange":     $orange,
  "yellow":     $yellow,
  "light-green":      $light-green,
  "green":      $green,
  "white":      $white,
  "teal":       $teal,
  "cyan":       $cyan,
  "white-transparent":       $white-transparent,
  "green-transparent":       $green-transparent,
) !default;
// scss-docs-end colors-map

// scss-docs-start theme-color-variables
$primary:       $green !default;
$secondary:      $yellow !default;
$tertiary:      $light-green !default;
$cyan:      $cyan !default;
$white-transparent:       $white-transparent !default;
$green-transparent:       $green-transparent !default;
$info:          $cyan !default;
$warning:       $yellow !default;
$danger:        $red !default;

// scss-docs-end theme-color-variables

// scss-docs-start theme-colors-map
$theme-colors: (
  "primary":    $primary,
  "secondary":  $secondary,
  "tertiary":  $tertiary,
  "cyan":  $cyan,
  "white-transparent":$white-transparent,
  "green-transparent":$green-transparent,
  "info":       $info,
  "warning":    $warning,
  "danger":     $danger,

) !default;

$font-size-root:              null !default;
$font-size-base:              1rem !default; // Assumes the browser default, typically `16px`
$font-size-sm:                $font-size-base * .875 !default;
$font-size-lg:                $font-size-base * 1.25 !default;

$font-weight-lighter:         lighter !default;
$font-weight-light:           300 !default;
$font-weight-normal:          400 !default;
$font-weight-bold:            700 !default;
$font-weight-bolder:          bolder !default;

$font-weight-base:            $font-weight-normal !default;

$line-height-base:            1.5 !default;
$line-height-sm:              1.25 !default;
$line-height-lg:              2 !default;

$h1-font-size:                $font-size-base * 2.5 !default;
$h2-font-size:                $font-size-base * 2 !default;
$h3-font-size:                $font-size-base * 1.75 !default;
$h4-font-size:                $font-size-base * 1.5 !default;
$h5-font-size:                $font-size-base * 1.25 !default;
$h6-font-size:                $font-size-base !default;
// scss-docs-end font-variables

// scss-docs-start font-sizes
$font-sizes: (
  1: $h1-font-size,
  2: $h2-font-size,
  3: $h3-font-size,
  4: $h4-font-size,
  5: $h5-font-size,
  6: $h6-font-size
) !default;

$grid-gutter-width: 20px;



// Grid breakpoints
//
// Define the minimum dimensions at which your layout will change,
// adapting to different screen sizes, for use in media queries.

// scss-docs-start grid-breakpoints
$grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 1080px,
  xl: 1400px,
  xxl: 2100px
) !default;
//BACKUPS SCREEN RATIO
/* $grid-breakpoints: (
  xs: 0,
  sm: 576px,
  md: 768px,
  lg: 992px,
  xl: 1200px,
  xxl: 1400px
) !default; */
// scss-docs-end grid-breakpoints

// Grid containers
//
// Define the maximum width of `.container` for different screen sizes.

// scss-docs-start container-max-widths
$container-max-widths: (
  sm: 540px,
  md: 720px,
  lg: 960px,
  xl: 1140px,
  xxl: 1320px
) !default;
// scss-docs-end container-max-widths