.clx {
   font-family: Chillax-variable;
}


.clx-brand {
   font-size: $grid-gutter-width;
   font-family: Chillax-variable;
   font-weight: 100;
   line-height: .9;
   @include media-breakpoint-up(lg) {
      font-size: $grid-gutter-width;
   }
   @include media-breakpoint-up(xl) {
      font-size: $grid-gutter-width*2;
   }
}


.clx-h2 {
   font-size: $grid-gutter-width*1.5;
   font-family: Chillax-variable;
   font-weight: 500;
   line-height: .9;
   @include media-breakpoint-up(lg) {
      font-size: $grid-gutter-width*2;
   }
   @include media-breakpoint-up(xl) {
      font-size: $grid-gutter-width*2.5;
   }
}
.clx-h3 {
   font-size: $grid-gutter-width*1.25;
   font-family: Chillax-variable;
   font-weight: 500;
   line-height: .9;
   @include media-breakpoint-up(lg) {
      font-size: $grid-gutter-width*1.5;
   }
   @include media-breakpoint-up(xl) {
      font-size: $grid-gutter-width*2;
   }
}



.clx-h4 {
   font-size: $grid-gutter-width;
   font-family: Chillax-variable;
   font-weight: 500;
   line-height: .9;
   @include media-breakpoint-up(lg) {
      font-size: $grid-gutter-width*1.15;
   }
   @include media-breakpoint-up(xl) {
      font-size: $grid-gutter-width*1.5;
   }
}


.ws-p {
   font-family: Work Sans;
   font-weight: 300;
   line-height: .99;
   font-size: $grid-gutter-width*0.75;
@include media-breakpoint-up(xl) {
   font-size: $grid-gutter-width*1.15;
}

}



.clx-link {
   text-decoration: none;
   font-size: $grid-gutter-width;
   font-family: Chillax-variable;
   font-weight: 500;
   line-height: .95;
   max-width: fit-content;
   &:hover
{
   padding-bottom: $grid-gutter-width*0.5;
   border-bottom: 2px solid;
}
}


.clx-button {
   text-decoration: none;
   font-size: $grid-gutter-width;
   font-family: Chillax-variable;
   font-weight: 500;
   line-height: .95;
   border: 2px solid;
   padding: $grid-gutter-width*0.75;
   border-radius: $grid-gutter-width;
   color: black;

   &:hover
{
   background-color: rgba(28, 82, 82, 1);
   color: white;

}
}









