
   @import "customs";
   @import "mixins";


  /* CUSTOMIZE THE CAROUSEL
-------------------------------------------------- */




/* Declare heights because of positioning of img element */


.carousel {

  .carousel-inner {

    .carousel-item {
      height: 100vh;
    img {
      position: absolute;
      top: 0;
      left: 0;
      min-width: 100%;
      height: 100vh;
    }

    .cos-carousel-container {

      .carousel-text {
        width: 35%;
        z-index: 100;
        color: white;
        border-right: 2px solid;
        background-color: rgba(50,50,50,0.1);
        backdrop-filter: blur(7px);


        h1 {
          font-family: Chillax-variable;
          font-size: $grid-gutter-width*5;
          font-weight: 200;

        }
        p {
          font-family: Chillax-variable;
          font-size: $grid-gutter-width*1.25;
          font-weight: 200;
        }
        a {
          color: white;
          text-decoration: none;
         
        }

      }
    }
  }
  }
}

