
   @import "customs";
   @import "mixins";


  .swap-link{
    position:absolute;
    bottom: $grid-gutter-width*4;
    right:$grid-gutter-width;
    text-decoration: none;
    color: white;
    border: 1px solid;
    border-radius: 10px;
    padding: $grid-gutter-width/3;
    font-size: $grid-gutter-width/1.5;
    font-family: Chillax-variable;
    backdrop-filter: blur(2px) contrast(60%);
    -webkit-backdrop-filter: blur(2px) contrast(60%);
    background-color: rgba(225, 225, 225, .2);
    border-radius: 10px;
  }
  .swap-link-mobile{
    text-decoration: none;
    color: white;
    border: 1px solid;
    border-radius: 10px;
    padding: $grid-gutter-width/3;
    font-size: $grid-gutter-width/1.5;
    font-family: Chillax-variable;
    backdrop-filter: blur(2px) contrast(60%);
    -webkit-backdrop-filter: blur(2px) contrast(60%);
    background-color: rgba(225, 225, 225, .2);
    border-radius: 10px;
  }



   #defaultCanvas0 { overflow:hidden; }
    .overlay {
    background-image: url("https://64.media.tumblr.com/96bfcda9ab211b730d680a052d19f83d/tumblr_o7w2veChcr1runoqyo4_540.gifv");
    height: 100%;
    width: 100%;
    opacity: 0.15;
    background-repeat:no-repeat;
    background-size: cover;
    position: absolute;
  } 
  

   .gos-general-infotab-g {
      display: fixed;
      background-color: transparent;
      width: 100vw;
      position: absolute;
      top:0;
      left:0;
        @include media-breakpoint-up(lg) {
          display: fixed;
          background-color: transparent;
          width: 100vw;
          height: 100vh;
          position: absolute;
          top:0;
          left: 0;  
        }

      .gos-general-infotab-container{
        height: 100%;
        color: aliceblue;
          @include media-breakpoint-up(lg) {

            }

          .gos-StationName {
             line-height: 1.2;

            .date {
              font-size: $grid-gutter-width;
              font-family: Chillax-Variable;
              font-weight: 300;
              @include media-breakpoint-up(lg) {
                font-size: $grid-gutter-width*2;
              }
        
            }
          
            h2 {
              font-size: $grid-gutter-width*1.5;
              font-family: Chillax-Variable;
              font-weight: 400;
              text-align: right;
