@import"customs";

  .gos-header{
      background-color: $secondary;
      height: $grid-gutter-width*5;
      width: 100vw;
      transition: all 0.3s;
      //breakpoint tablet
      //breakpoint desktop
      
      
  
          .gos-sidebar-link-logo{
  
              h1 {
                 color: $primary;
                 font-family: work sans;
                 font-size: $grid-gutter-width*1;
                 font-weight: 600! important;
                 @include media-breakpoint-up(md) {
                  font-size: $grid-gutter-width*1.5;

                 }

              }
      }

      .gos-sidebar-button{
        position:relative;
        height: 100%;
        width: 100%;
        text-align: -webkit-right;
        .hamburger{
            height: 100%;
            width: 100%;
            left: 0;
            top: 0;
        }
      }


      .offcanvas-end{
        border-radius: 50px 0 0 20px;
          background-color: $secondary;
          
          width: 100vw;
          height: 70vh;
          @include media-breakpoint-up(md) {
            width:80vw; 
            height: 100vh;
         
          }

          .offcanvas-container{
              .offcanvas-container-menu {

                .offcanvas-header {
                    border-bottom: 2px solid $primary;
                    h5{
                      font-family: Work sans;
                      font-size: $grid-gutter-width;
                      font-weight: 200;
                      color: $primary;

                      @include media-breakpoint-up(md) {
                        font-size: $grid-gutter-width*3;
                      }

                    }
                    .btn-close{

                    }
                  }
             }

             .offcanvas-container-info {
               
               border-right: 2px solid $primary;

             }
          }

            .menu-items{

                a {
                  @include gos-navbar-link;
                }
            }

            
      }
      
  }
  
  
  .top-of-page {
      
      background-color: $tertiary;
      height: $grid-gutter-width*5;
      width: 100vw;
      }
  
      .blurred{
          webkit-backdrop-filter: blur(10px);
      backdrop-filter: blur(10px);
      }




      ////////ICON MENUUU////

      .icon-container {
        display: inline-block;
        cursor: pointer;
      }
      
      .bar1, .bar2, .bar3 {
        width: 35px;
        height: 5px;
        background-color: $white;
        margin: 6px 0;
        transition: 0.4s;
        border-radius:20px;
      }
      
      .change .bar1 {
        -webkit-transform: rotate(-45deg) translate(-9px, 6px);
        transform: rotate(-45deg) translate(-9px, 6px);
      }
      
      .change .bar2 {opacity: 0;}
      
      .change .bar3 {
        -webkit-transform: rotate(45deg) translate(-8px, -8px);
        transform: rotate(45deg) translate(-8px, -8px);
      }



      