@import"customs";


.cos-hamburger{

border-radius: 10px;
padding: $grid-gutter-width/2;
webkit-backdrop-filter: blur(10px);
        backdrop-filter: blur(10px); 
        background-color: rgba(225, 225, 225, .2);
        border-radius: 10px;
}


.cos-sidebar {


  background-color: rgba(217, 217, 217, 1);
  backdrop-filter: blur(10px);
  width:100vw;
  height:100vh;

  .cos-sidebar-header {
    position: fixed;
    right: $grid-gutter-width;
    top: $grid-gutter-width*0.75;
    border: 2px solid;
    border-radius:100px;
    padding: $grid-gutter-width/2;
  }

  .cos-sidebar-body {
    padding: 0px;
    

    .cos-sidebar-accordion-container {
      background-color: transparent;


      .cos-sidebar-accordion-item {
        background-color: transparent;

        .cos-sidebar-accordion-header {

          h2 {
            background-color: transparent;
            color: $primary;
            font-family: Chillax-variable;
            font-weight: 700;
            font-size: $grid-gutter-width*0.75;
            padding: $grid-gutter-width*1.25;
            border-top: 2px solid;
          }

        }
        .cos-sidebar-accordion-body { 
          background-color: transparent;
          overflow: scroll;
          height: 70vh;
          @include media-breakpoint-up(lg) {
            height: 73vh;
          }
          
            
            .municipio{
              font-family: Oswald;
              font-size: $grid-gutter-width*1.5;
              font-weight: 300;
              text-decoration: none;
              color: black;
            }
          
         

          

          .body-inner{

            h4{
              font-family: Chillax-variable;
              font-size: $grid-gutter-width;
              font-weight: 400;
              @include media-breakpoint-up(lg) {
                font-size: $grid-gutter-width*2;
              }
  
            }


            h2{
              font-family: Chillax-variable;
              font-size: $grid-gutter-width*1.25;
              font-weight: 400;
            }
            h3{
              font-family: Chillax-variable;
              font-size: $grid-gutter-width;
              font-weight: 400;
            }
          
            
          }

          .container-dati{
            .sentinella-personale-inner{
              .sentinella-heading{

              }
              .sentinella-sensors{

                .sensore{
                  .h3{

                  }
                  ul{
                    list-style-type: none;
                    font-family: Work Sans;
                    padding: 0;
                  }

                }
                
              }
            }
          }
          
          }


        }

      }

    }

  }


::-webkit-scrollbar {
  display: none;
}

.sel{
  border: 2px solid black;
  border-radius: 30px;
}