 }

  .gos-footer {
    width: 100vw;
    height: $grid-gutter-width*3;
    position: absolute;
    bottom: 0px;
    border-top: 2px solid white;
    backdrop-filter: blur(2px) contrast(60%);
    -webkit-backdrop-filter: blur(2px) contrast(60%);  
    background-color: rgba(27, 35, 35, 0.219);


      .gos-footer-container {

        .gos-footer-inner{

