// 
// Navbar
// 

#mainNav {
    height: 4.5rem;
    background-color: rgba(28, 82, 82, 1);

    .navbar-toggler {
        font-size: 80%;
        padding: 0.75rem;
        color: $primary;

        &:focus {
            outline: none;
        }
    }

    .navbar-brand {
        color:white;
        padding: 0.9rem 0;
    }

    .navbar-nav {
        .nav-item {
            font-family: Chillax-variable;
            &:hover {
                outline: none;
                background-color: transparent;
            }

            &:active,
            &:focus {
                outline: none;
                background-color: transparent;
            }
        }
        &.navbar-shrink {
            background-color: rgba(28, 82, 82, 0.482);
            backdrop-filter: blur(5px);
        }

    }
    
    

    @include media-breakpoint-up(lg) {
        padding-top: 0;
        padding-bottom: 0;
        border-bottom: none;
        background-color: rgba(200, 200, 200, 0);
        transition: background-color 0.5s ease-in-out;

        .navbar-brand {
            padding: 0.5rem 0;
            color: fade-out(white, 0.5);
        }

        .nav-link {
            transition: none;
            padding: 2rem 1.5rem;
            color: fade-out(white, 0.5);

            &:hover {
                color: fade-out($white, 0.25);
            }

            &:active {
                color: white;
            }
        }

        &.navbar-shrink {
            background-color: rgba(28, 82, 82, 0.482);
            backdrop-filter: blur(5px);


            .navbar-brand {
                color: white;
            }

            .nav-link {
                color: white;
                padding: 1.5rem 1.5rem 1.25rem;
                border-bottom: 0.25rem solid transparent;

                &:hover {
                    color: $primary;
                }

                &:active {
                    color: darken($primary, 15%);
                }

                &.active {
                    color: $primary;
                    outline: none;
                    border-bottom: 0.25rem solid $primary;
                }
            }
        }
    }
}





