.footer {
    background-color: rgba(28, 82, 82, 0.482);
    .footer-list{
        list-style-type: none;
        text-align:left;
        color: black;
        text-align: center;
        @include media-breakpoint-up(md) {
            text-align: right;
        }

        .footer-item{
            text-decoration: none;
            color:black;
            font-family:Chillax-variable;
            font-size: $grid-gutter-width*0.75;
        }
    }
}